html, body, #wrap { height: 100%; }pre { tab-size: 4 !important; }#wrap { background-color: #f6f6f6; }#content { background-color: #FFC44F; }#content:before { position: absolute; top: 4rem; left: 0; right: 0; bottom: 0; max-width: 60vw; margin: 0 auto; background-color: #ececec; content: ''; }.list_content { display: flex; position: relative; max-width: 60vw; margin: 0 auto; }.list_content > li { flex: 1 0 auto; }.list_content > li + li { border-left: 1px solid rgba(0, 0, 0, 0.2); }.list_content > li.on .btn_tab { background-color: #dda53e; }.list_content > li > section { position: absolute; top: 4rem; left: 0; right: 0; max-width: 60vw; }.list_content .btn_tab { display: block; width: 100%; height: 4rem; background-color: #f4b53f; color: #fff; font-size: 1.65rem; text-align: center; }